<template>
  <div class="cart-count">
      <div class="count-wrapper">
          <check-ed :is-checked="allchecked"  @click.native="checkallbtn"></check-ed><span></span>
      </div>
      <div class="count-submit">
          <span class="count-num"><b>{{totalPrice}}</b></span>
          <span class="submit-btn">({{checkedLength}})</span>
      </div>
  </div>
</template>

<script>
//
import CheckEd from "components/common/check/CheckEd";
import { mapGetters } from "vuex";
export default {
  name: "CartCount",
  components:{
      CheckEd
  },
  computed:{
       ...mapGetters(['cartlistdata']),
       // (pre)(item callback )
        totalPrice() {
            return '' + this.cartlistdata
                .filter(item => item.checked)
                .reduce((pre, item) => pre + item.price * item.count, 0)
                .toFixed(2);
        },

        //
        checkedLength() {
            return this.cartlistdata.filter(item => item.checked).length;
        },

        //
        allchecked(){
            if(this.cartlistdata.length === 0 ) return false
            //1find
            //return !this.cartlistdata.find(item => !item.checked) 
            //2filter
            //return !(this.cartlistdata.filter(item => !item.checked).length) 
            //3for
            for(let item of this.cartlistdata){
                //
                if(!item.checked){
                    return false
                }
            }
            return true
            
        }
  },
  methods:{
      //
      checkallbtn(){
          if(this.allchecked){ //allchecked
                this.cartlistdata.forEach(item => item.checked = false)
          }else{
                this.cartlistdata.forEach(item => item.checked = true)
          }

      }
  }

};
</script>
<style lang="scss" scoped>
.cart-count{
    position: fixed;
    bottom: 43px;
    bottom: calc(43px + constant(safe-area-inset-bottom));
    bottom: calc(43px + env(safe-area-inset-bottom));
    width: 100%;
    max-width: 768px;
    z-index: 999;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    padding: 0 12px;
    background-color: #FFFFFF;
    height: 54px;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    .count-wrapper{
        white-space: nowrap;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        margin-right: 10px;
    }
    .count-submit{
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        -webkit-box-pack: end;
        -webkit-justify-content: flex-end;
        justify-content: flex-end;
        overflow: hidden;
        .submit-btn{
            background: linear-gradient(90deg,#27a6fa,#8e71f5);
            background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
            font-size: 15px;
            line-height: 15px;
            padding: 8px 18px;
            text-align: center;
            border-radius: 80px;
            color: #FFFFFF;
            white-space: nowrap;
            margin-left: 10px;
        }
        .count-num{
            font-size:14px;
            b{
                color:red;
            }

        }
    }
}
</style>